import Vue from 'vue'
import VueRouter from 'vue-router'
import Showallvideo from '@/views/Showallvideo'
import Playvideo from '@/views/Playvideo'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    redirect: '/Showallvideo'
  },
  {
    path: '/Showallvideo',
    name: 'Show All Videos',
    component: Showallvideo
  },
  {
    path: '/Playvideo',
    name: 'Play Video',
    component: Playvideo
  }
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
