<template>
<v-container class="containerwidth">
    <v-row justify="center">
        <v-col cols="4" lg="2" md="3" sm="4" v-for="(i, index) in 30" :key="index">
            <button class="filmbtn" @click="showFilm">
                <img src="../assets/Frozen-movie-poster.jpg" class="imgsize" />
                <v-icon class="playicon">mdi-play-circle-outline</v-icon>
                <v-card-title>{{ title }}</v-card-title>
            </button>
        </v-col>
    </v-row>
</v-container>
</template>

<script>
export default {
    data() {
        return {
            title: 'FROZEN'
        }
    },
    methods: {
        showFilm() {
            this.$store.state.videourl = 'http://https://cdn.plyr.io/static/demo/View_From_A_Blue_Moon_Trailer-576p.mp4'
            this.$router.push({
                path: '/Playvideo'
            })
        }
    }
}
</script>
